<template id="roof">
  <div>
    <h1>Risk Grading System</h1>
    <input type="text" v-model="msg">
    <button @click="showChildComponentData">评估</button>
    <P> 已收录 {{ number }} 家机构 </P>
    <router-link to="/About">About</router-link>
    <div id="echartContainer2" style="width:500px; height:500px"></div>
  </div>
</template>

<script type="text/javascript">
import $ from 'jquery'
var option = {
  title: { text: '简单柱状图' },
  tooltip: {},
  xAxis: {
    type: 'category'
  },
  yAxis: {
    type: 'value'
  },
  series: [{
    name: '销量',
    type: 'bar',
    itemStyle: {
      normal: {
        color: 'hotpink'
      }
    }
  }]
}
export default {
  name: 'bar-chart',
  data () {
    return {
      number: '18723'
    }
  },
  mounted () {
    this.$nextTick(function () {
      this.drawBarChart()
    })
  },
  methods: {
    drawBarChart () {
      var myChart = this.$echarts.init(document.getElementById('echartContainer2'), 'infographic')
      $.ajax({
        type: 'post',
        async: false,
        url: '/servlet/helloWorld',
        data: {},
        dataType: 'json',
        success: function (result) {
          if (result) {
            option.xAxis[0].data = []
            for (var i = 0; i < result.length; i++) {
              option.xAxis[0].data.push(result[i].name)
            }
            option.series[0].data = []
            for (var j = 0; j < result.length; j++) {
              option.series[0].data.push(result[j].num)
            }
          }
        }
      })
      myChart.setOption(option)
    },
    showChildComponentData: function () {
      this.$store.state.keyword = this.msg
      this.$router.push({path: '/Result'})
    }
  }
}
</script>

<style>
</style>



