<template id="view2">
  <div>
    <div id="gaugeContainer" style="width:500px; height:500px"></div>
  </div>
</template>

<script>
const echarts = require('echarts')
export default {
  name: '',
  data () {
    return {
    }
  },
  mounted () {
    let myChart = echarts.init(document.getElementById('gaugeContainer'))
    myChart.setOption({
      tooltip: {
        formatter: '{a} <br/>{b} : {c}%'
      },
      toolbox: {
        show: true,
        feature: {
          mark: {
            show: true
          },
          restore: {
            show: true
          },
          saveAsImage: {
            show: true
          }
        }
      },
      series: [
        {
          name: '业务指标',
          type: 'gauge',
          detail: {
            formatter: '{value}%'
          },
          data: [
            {
              value: 50,
              name: '工作比'
            }
          ]
        }
      ]
    })
  }
}
</script>

<style scoped>

</style>



