<template>
  <div id="view1">
  <!--创建一个echarts的容器-->
    <div id="echartContainer" style="width:500px; height:500px"></div>
  </div>
</template>

<script>
var echarts = require('echarts')
export default{
  name: 'line-chart',
  data () {
    return {
    }
  },
  mounted () {
    var myChart = echarts.init(document.getElementById('echartContainer'))
    myChart.setOption({
      tooltip: {},
      xAxis: {
        data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
      },
      yAxis: {},
      series: [{
        name: '销量',
        type: 'bar',
        data: [5, 20, 36, 10, 10, 20],
        itemStyle: {
          normal: {
            color: 'hotpink'
          }
        }
      }],
      title: {
        show: true,
        text: '简单折线图',
        link: 'http://echarts.baidu.com/option.html#title.link',
        subtext: '熟悉title的配置项',
        left: 50,
        padding: [5, 20, 5, 10]
      },
      toolbox: {
        show: true,
        orient: 'vertical',
        itemSize: 18,
        itemGap: 20,
        right: 20,
        feature: {
          saveAsImage: { show: true },
          dataView: { show: true },
          magicType: {
            type: [ 'line', 'bar' ]
          },
          dataZoom: { show: true },
          restore: { show: true }
        }
      }
    })
  }
}
</script>

<style>
</style>



